.controlButton {
  color: grey;
  padding: 10px 15px;
  margin: 5px 5px;
  border-radius: 5px;
  border: 2px solid rgba(19, 209, 215, 1);
  background: rgba(19, 209, 215, 0.2);
  transition: 0.3s;
  cursor: pointer;
}
.counterText {
  color: grey;
  text-shadow: 2px 2px 3px rgb(185, 183, 183);
}
